import MUtil from '@/utils/mm.js'

const login = {
  //
  login(data){
    return MUtil.request('/user/login',data)
  },
  //
  logout(){
    return MUtil.request('/user/logout',{},'get')
  },
  //
  getUserInfo(){
    return MUtil.request('/user/getInfo',{},'get')
  }
};


export default login



