<template>
  <div>
    <div class="centerTitle">
      
    </div>
    <div class="searchCon">
      <div class="searchItem">
        <div class="searchLabel"></div>
        <div class="searchInput">
          <el-input class="searchItem" v-model="search.orderUUid" placeholder=""></el-input>
        </div>
      </div>
      <div class="searchItem">
        <div class="searchLabel"></div>
        <div class="searchInput">
          <el-input class="searchItem" v-model="search.title" placeholder=""></el-input>
        </div>
      </div>
      <div class="searchBtnCon">
        <el-button type="primary" class="CoBtn" @click="searchList()"></el-button>
        <el-button class="CoResetBtn" @click="reset()"></el-button>
      </div>
    </div>
    <div class="btnGroupCon">
      <el-button type="primary" class="CoBtn" @click="goAddGoods()"><div class="excelIcon"></div><div class="exportText"></div></el-button>
    </div>
    <div class="totalTableSize">
      <div class="tipIcon"></div>
      <div class="totalCon">
        {{page.total}}
      </div>
    </div>
    <div class="tableCon">
      <commonTable :columns="columns" :data="tableData" :pager="page"
                   @handleCurrentChange="handleCurrentChange">

         <el-table-column slot="table_oper" align="center" label="" width="80" :resizable="false" fixed="right">
          <template slot-scope="scope">
            <el-badge is-dot class="dots" type="success"></el-badge>
            <span v-if="scope.row.orderStatus === 'Close'"></span>
            <span v-else-if="scope.row.orderStatus === 'Pending'"></span>
            <span v-else-if="scope.row.orderStatus === 'Complete'"></span>
          </template>
        </el-table-column>

        <el-table-column slot="table_oper" align="center" label="" width="200" :resizable="false" fixed="right">
          <template slot-scope="scope">
            <div class="tableBtn">
              <el-button type="text" @click="detailPop(scope.row.orderUUid)"></el-button>
            </div>
          </template>
        </el-table-column>
      </commonTable>
    </div>
  </div>
</template>

<script>
  import ORDER from 'admin/service/order-service.js'
  export default {
    name: 'OrderMg',
    data() {
      return {
        search : {
          orderUUid : '',
          title : ''
        },
        selectOptions : [
          {
            value: '1',
            label: ''
          }, {
            value: '2',
            label: ''
          }
        ],
        columns: [
          {prop: 'orderUUid', label: '', width: '200', align: 'left'},
          {prop: 'title', label: '', width: '150', align: 'left'},
          {prop: 'goodPrice', label: '', align: 'right',width: '80'},
          {prop: 'coin', label: '', align: 'right',width: '96'},
          {prop: 'created_at', label: '', align: 'right',width: '120'}
        ],
        tableData: [],
        page: {
          page: 1,
          unit: 10,
          total: 0
        },
      }
    },
    mounted() {
      this.page.total = this.tableData.length;
    },
    created(){
      this.getList();
    },
    methods: {
      //
      getList(){
        let data ={
          'orderUUid':this.search.orderUUid,
          'title':this.search.title,
          'page' : this.page.page,
          'unit': 10
        }
        ORDER.getOrderList(data)
          .then(res => {
            console.log('res',res);
            if(res.code === "0"){
              this.tableData = res.data;
              this.page.total = res.count;
            }
          })
      },
      //
      searchList(){
        this.page.page = 1;
        this.getList();
      },
      //
      reset(){
        this.search.title = '';
        this.search.orderUUid = '';
      },
      // 
      handleCurrentChange(val) {
        this.page.page = val;
        this.getList();
      },
      //
      detailPop(){
        this.$router.push({
          path: '/goods/orderMg/orderDetail',
          name:'OrderDetail'
        })
      }
    }
  }
</script>


<style lang="css" scoped>
  .searchInput{
    width: 256px;
    height: 32px;
    float: left;
  }
  .excelIcon{
    width: 16px;
    height: 16px;
    background:url(~admin/assets/excel.png) no-repeat;
    background-size:contain;
    float: left;
  }
  .exportText{
    float: left;
    padding-top: 2px;
  }

</style>



