import MUtil from '@/utils/mm.js'

const order = {
  //
  getOrderList(data){
    return MUtil.request('/order/getMyOrders',data)
  },
  //
  getOrderDetail(data){
    return MUtil.request('/order/findOrder',data)
  }
};


export default order



